import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";

export class MonitorTicsScreen extends Component {
    render() {
        return (
            <Category
                title={strings.monitor_tics__title}
                subtitle={strings.monitor_tics__subtitle}>

                <IncomingTextMessage
                    text={strings.monitor_tics__app}
                    sender={strings.app} />

                <OutcomingTextMessage
                    text={strings.monitor_tics__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/eye_tics.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}