import React, { Component} from 'react';
import strings from '../Localisation';
import { IncomingTextMessage } from '../messages/TextMessages/IncomingTextMessage';
import { Category } from '../Category';
import {OutcomingTextMessage} from "../messages/TextMessages/OutcomingTextMessage";
import {IncomingGifMessage} from "../messages/GifMessages/IncomingGifMessage";

export class IFTTTIntegrationScreen extends Component {
    render() {
        return (
            <Category
                title={strings.ifttt_integration__title}
                subtitle={strings.ifttt_integration__subtitle}>

                <IncomingTextMessage
                    text={strings.ifttt_integration__smart_tv}
                    sender={strings.smart_tv} />

                <OutcomingTextMessage
                    text={strings.ifttt_integration__user}
                    sender={strings.user}/>

                <IncomingGifMessage
                    path="/images/gifs/ifttt_integration.gif"
                    sender={strings.app}/>

            </Category>
        )
    }
}